@import "../../../../Common/Styles/variables";
@import "../../../../Common/Styles/mixins";
.registerWrapper {
  @include windows();

  .h1 {
    /*border: 1px solid black;*/
    /*margin-top: -109px;*/
  }

  .errors {
    color: red;
    font-size: 14px;
    display: flex;
    justify-content: center;
    /*border: 1px solid black;*/
  }

  .textFields {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    width: 280px;
    /* border: 1px solid black;*/
  }

  .buttons {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    position: relative;
    /*border: 1px solid black;*/
    width: 280px;
    height: 80px;

    .circularProgress {
      position: absolute;
      height: 36px;
      top: 22px;
      left: 190px;
    }

    .cancelButton {
      position: absolute;
      background-color: #D7D8EF !important;
      height: 36px;
      width: 124px;
      margin-right: 2rem;
      opacity: 0.8;
      top: 22px;
      left: 0;
      color: #21268F;
    }

    .registerButton {
      opacity: 0.8;
      top: 22px;
      height: 36px;
      width: 124px;
      position: absolute;
      background-color: #21268F !important;
      margin-right: 0;
      left: 156px;
      color:  #ECECF9 !important;
    }

    .registerButton:hover {
      background-color: #4448c2!important;
    }

    .cancelButton:hover {
      background-color: #c9caec !important;
    }

  }
}
